<template>
  <div>
    <div class="flex items-center justify-between mb-5">
      <div
        v-for="{ key, name } in dataSource"
        :key="key"
        @click="handleClickTab(key)"
        class="text-center flex-1 py-1 lg:py-4 border-b-4 lg:border-b-8 border-solid cursor-pointer text-base lg:text-2xl hover:text-tab-active-color hover:border-main-red"
        :class="{
          'text-tab-color': selectTab !== key,
          'border-tab-border-color': selectTab !== key,
          'text-tab-active-color': selectTab === key,
          'border-main-red': selectTab === key,
        }"
      >
        {{ name }}
      </div>
    </div>
    <div v-for="{ key } in dataSource" v-show="selectTab === key" :key="key">
      <slot :name="key"></slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dataSource: {
      type: Array,
      default: () => [],
    },
    defaultTab: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      selectTab: "",
    };
  },
  methods: {
    handleClickTab(key) {
      this.selectTab = key;
      this.$emit("onClick", key);
    },
  },
  watch: {
    defaultTab: {
      immediate: true,
      handler(value) {
        this.selectTab = value;
      },
    },
  },
};
</script>
